<template>
  <div class="about">
    <h1>메모입니다</h1>
    <table>
      <tr>
        <td>번호</td>
        <td>제목</td>
        <td>내용</td>
        <td>작성자</td>
      </tr>
      <!--12. 저장된 memo출력-->
      <tr v-for="item in memo" :key="item.id">
        <td>{{item.id}}</td>
        <!--title을 클릭했을때 pageview화면으로 넘어감-->
        <!--보여지는 화면은 vue의 화면이기 때문에 뷰 컴포넌트와 라우터가 존재해야함-->
        <td @click="$router.push(`/about/${memo.id}`)">{{item.title}}</td>
        <td>{{item.memo}}</td>
        <td>{{item.writer}}</td>      
      </tr>
    </table>
  </div>
</template>

<script>
/*
(2) about링크를 눌렀을 때 AboutView.vue컴포넌트 호출
(3) axios를 통해서 backend(/app/memo)에 http객체로 request를 보냄

*/

export default {
  //10. created()에 생성
  created() {
    //메모값을 create에 연결 : vue가 생성될때 가져오기
    //axios사용방법은 fetch와 거의 동일
    //주소 '/api/memo'로 get메소드로 request를 보냄
    this.$http.get('/api/memo')
      .then((response)=>{ 
        //response이 오면 아래 내용을 실행
        console.log(response.data)
        //11. response값을 component data의 memo에 저장
        this.memo = response.data
      })
      /*
      (6) memo.js에서 보내준 memos의 값을 response를 통해 전달 받음
      */
  },
  data () {
    return {
      memo : []
    }
  }
}
</script>
